<template>
  <div class="vuedt">
    <input type="date" :value="isoDate" />
    <button @click="active = !active">&#128197;</button>
    <div class="pickers" v-show="active">
      <date-picker v-model="dateTriple" />
      <time-picker v-model="timeTriple" />
    </div>
  </div>
</template>

<script>
import DatePicker from './VuedtCalendar.vue'
import TimePicker from './VuedtClock.vue'

export default {
  name: 'vuedt',
  components: { DatePicker, TimePicker },
  props: { value: { type: Date, default: () => new Date() } },
  data () {
    return {
      dateTriple: { y: 0, m: 0, d: 0 },
      timeTriple: { H: 0, M: 0, S: 0 }
    }
  },
  computed: {
    isoDate () {
      const {y, m, d} = this.dateTriple
      const {H, M, S} = this.timeTriple
      const date = new Date(y, m, d, H, M, S)

      return date.toISOString()
    }
  }
}
</script>

<style scoped>
.vuedt > input {
  width: 200px;
  text-align: center;
}
.pickers {
  position: absolute;
  z-index: 99999;
}
.pickers > .calendar,
.pickers > .clock {
  display: inline-block;
  vertical-align: top;
  min-width: 252px;
  border: 1px solid #666;
  background: #FFE;
}
</style>



